.chat {
    .chat__tabs a.active {
        @apply bg-theme-1 text-white;
    }
    .chat__chat-list {
        height: 525px;
    }
    .chat__user-list {
        height: 525px;
    }
    .chat__box {
        height: 782px;
        .chat__box__text-box {
            max-width: 49%;
            @media (max-width: $sm) {
                max-width: 90%;
            }
        }
    }
    .chat__box__input {
        height: 46px;
    }
}